const ImageContainer = ({ title, thumbs, isLeft }) => {
  const flickityOptions = {
    initialIndex: 0,
    cellAlign: 'left',
    wrapAround: true,
    groupCells: true,
    lazyLoad: 1,
    pageDots: TextTrackCue,
    draggable: window.innerWidth < 768 ? true : false,
    adaptiveHeight: false,
    imagesLoaded: false,
    fullscreen: true,
    autoPlay: false,
  };

  console.log('thumbs', thumbs);
  return (
    <div className={`image-container ${isLeft ? 'left-card' : ''}`}>
      <Flickity
        className={'carousel'} // default ''
        elementType={'div'} // default 'div'
        options={thumbs.length > 1 ? flickityOptions : {}} // takes flickity options {}
        disableImagesLoaded={false} // default false
        reloadOnUpdate // default false
        static // default false
      >
        {thumbs.map((thumb) => {
          return (
            <Image
              style={{
                height: 'auto',
                marginTop: '2rem',
              }}
              className="project-thumb w-100"
              src={thumb}
              alt={title}
            />
          );
        })}
      </Flickity>
    </div>
  );
};
