let isGTMInitiated = false;

// Init Google Tag Manager
export const GTMInit = () => {
  //Inserting Tag manager scripts
  document.head.insertBefore(getInitScript(), document.head.childNodes[0]);
  document.body.insertBefore(getInitNoScript(), document.body.childNodes[0]);
  isGTMInitiated = true;
};

export const setVirtualPage = (pagePath, pageTitle) => {
  setDataLayer({
    dataLayer: {
      event: 'VirtualPageView',
      vitualPagePath: pagePath,
      virtualPageTitle: pageTitle,
      virtualPageLocation: `${window.origin}/${pagePath}`,
    },
  });
};

export const setEvent = (category, action, label) => {
  setDataLayer({
    dataLayer: {
      event: 'Video',
      eventCategory: category,
      eventAction: action,
      eventLabel: label,
    },
  });
};

//Set DataLayer Values
const setDataLayer = ({ dataLayer, dataLayerName = 'dataLayer' }) => {
  if (isGTMInitiated) {
    if (window[dataLayerName]) {
      return window[dataLayerName].push(dataLayer);
    } else {
      const snippets = `window.${dataLayerName} = window.${dataLayerName} || [];
      window.${dataLayerName}.push(${JSON.stringify(dataLayer)})`;
      const script = document.createElement('script');
      script.innerHTML = snippets;
      document.head.appendChild(script);
    }
  }
};

//Return noscript element with google-tag iframe code snippet
const getInitNoScript = () => {
  const noscript = document.createElement('noscript');
  noscript.innerHTML = `<iframe src='https://www.googletagmanager.com/ns.html?id=GTM-T6DVZPC' height='0' width='0' style='display:none;visibility:hidden'></iframe>`;
  return noscript;
};

//Return script element with google-tag script code snippet
const getInitScript = () => {
  const script = document.createElement('script');
  script.innerHTML = `(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src='https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);})(window,document,'script','dataLayer','GTM-T6DVZPC');`;
  return script;
};
